import './magazine.scss';
import React from 'react';
import { graphql } from 'gatsby';
import Page from 'src/layout/Page';
import SEO from 'components/seo';
import { Container, Row, Col, Button } from 'react-bootstrap';

export const pageQuery = graphql`
  query($path: String!) {
    markdownRemark(frontmatter: { path: { eq: $path } }) {
      html
      frontmatter {
        date(formatString: "MMMM DD, YYYY")
        path
        title
      }
    }
  }
`;

export default function Template({ data }) {
  const { markdownRemark } = data;
  if(!markdownRemark) {
    return <div>Render Error</div>;
  }
  const { frontmatter, html } = markdownRemark;

  return (
    <Page pageName="post">
      <SEO title={frontmatter.title} />
      <Container data-type="markdown">
        <Row>
          <Col xs={12} sm={12} md={12} lg={12}>
            <div className="title">
              <h1>{frontmatter.title}</h1>
              <p>{frontmatter.date}</p>
            </div>
            <div className="contents" dangerouslySetInnerHTML={{ __html: html}}></div>
          </Col>
        </Row>
        <Row className="post-footer">
          {/* <Col>
            <Button variant="dark">이전</Button>
          </Col> */}
          <Col>
            <Button variant="secondary" href="/">홈으로</Button>
          </Col>
          {/* <Col>
            <Button variant="dark">다음</Button>
          </Col> */}
        </Row>
      </Container>
    </Page>
  );
};
